import React from "react";
import { Container, Grid, Stack, Typography } from "@material-ui/core";
import { styled } from "@material-ui/styles";
import Carousel from "react-elastic-carousel";
import VideoItem from "./VideoItem";

import SponseredGoalsItem from "./SponseredGoalsItem";
import {
  SponserGoalsDonorType,
  GoalsaveUserType,
} from "../../../../../utils/typeHelpers";
import { useWindowDimensions } from "../../../../../utils/helperFunction";
import MyArrow from "../../../../../components/MyArrow";
import CustomPagination from "../../../../../components/CustomPagination";

const RIghtContainer = ({
  supportedGoals,
  savedGoals,
}: {
  supportedGoals: SponserGoalsDonorType[];
  savedGoals: GoalsaveUserType[];
}) => {
  const { width } = useWindowDimensions();

  return (
    <Grid item xs={12} sm={12} md={8} sx={{ backgroundColor: "#F9F9F9" }}>
      <Stack
        direction="column"
        justifyContent="center"
        alignItems="flex-start"
        spacing={1}
        sx={{ padding: 1 }}
      >
        {supportedGoals && supportedGoals?.length > 0 && (
          <Container>
            <SpanText>Sponsored Goals</SpanText>
            <Carousel
              isRTL={false}
              itemsToShow={width < 500 ? 2 : 5}
              renderArrow={MyArrow}
              renderPagination={({ pages, activePage, onClick }) => (
                <CustomPagination
                  pages={pages}
                  activePage={activePage}
                  onClick={onClick}
                />
              )}
            >
              {supportedGoals &&
                supportedGoals.map((item: SponserGoalsDonorType) => (
                  <SponseredGoalsItem
                    slug={item.goal.slug}
                    image={item.goal.image}
                    id={item.goal.id}
                    title={item.goal.title}
                  />
                ))}
            </Carousel>
          </Container>
        )}

        {savedGoals && savedGoals?.length > 0 && (
          <Container>
            <TitlePinned>
              <SpanText>Pinned Goals</SpanText>
            </TitlePinned>
            <Grid
              container
              direction="row"
              justifyContent="center"
              alignItems="center"
            >
              {savedGoals.map((item: GoalsaveUserType) => (
                <VideoItem image={item.goal.image} slug={item.goal.slug} />
              ))}
            </Grid>
          </Container>
        )}
      </Stack>
    </Grid>
  );
};

export default RIghtContainer;

const TitlePinned = styled(Typography)({
  color: "#6EEDD6",
  fontSize: 22,
  fontWeight: 600,
});

const SpanText = styled("span")({
  color: "#000",
  fontSize: 22,
  fontWeight: 600,
});
const ScrollList = styled("div")({
  flexDirection: "row",
  display: "flex",
  overflow: "hidden",
  scrollBehavior: "smooth",
});
