import { Avatar, Grid, Stack, Typography } from "@material-ui/core";
import React from "react";
import { styled } from "@material-ui/styles";

import WorkAvatar from "../../../../../assets/cards/work.png";
import InfoButton from "./InfoButton";
import BasicInfo from "./BasicInfo";
import DonationContainer from "./DonatioNContainer";
import TeamDonationItem from "./TeamDonationItem";
import GiveDonation from "./GiveDonation";
import { UserDetailProps } from "../../../../../utils/typeHelpers";

interface ProfileProps {
  userInfo: UserDetailProps;
  handleRefresh: ()=>void;
}

const LeftContainer = ({ userInfo, handleRefresh }: ProfileProps) => {
  return (
    <Grid item xs={12} sm={12} md={4}>
      <BasicInfo userInfo={userInfo} handleRefresh={handleRefresh}/>
      <Stack
        direction="column"
        justifyContent="flex-start"
        alignItems="flex-start"
        sx={{ padding: 3 }}
        spacing={1}
      >
        <Typography variant="subtitle1">About me</Typography>
        <Stack direction="row" justifyContent="center" alignItems="center">
          <Avatar
            alt="ProfileAvatar"
            src={WorkAvatar}
            sx={{ width: 40, height: 40 }}
          />
          <WorksAt>Works at Domino`s Pizza</WorksAt>
        </Stack>
        <Typography variant="body2">{userInfo.bio}</Typography>
        <Grid
          container
          direction="row"
          justifyContent="space-around"
          alignItems="center"
        >
          <InfoButton
            label="Goals Supported"
            number={userInfo.total_supported_goals}
          />
          <InfoButton
            label="Total donations"
            number={userInfo.total_donated_amount}
          />
          <InfoButton
            label="Total Supported Goals"
            number={userInfo.total_supported_goals}
          />
        </Grid>
      </Stack>
      <Grid
        container
        direction="column"
        justifyContent="center"
        alignItems="center"
      >
        <DonationContainer />
        <div style={{ marginTop: 10 }} />
        <TeamDonationItem />
        <div style={{ marginTop: 10 }} />
        <GiveDonation />
      </Grid>
    </Grid>
  );
};

export default LeftContainer;

const WorksAt = styled(Typography)({
  color: "#6C757D",
  fontSize: 16,
  fontWeight: 400,
  paddingLeft: 10,
});
