import { Stack, Typography } from "@material-ui/core";
import { styled } from "@material-ui/styles";
import React from "react";
import { useNavigate } from "react-router";
import { SponseredGoalType } from "../../../../../utils/typeHelpers";

const SponseredGoalsItem = ({ image, title, id, slug }: SponseredGoalType) => {
  const navigate = useNavigate();
  const viewGoalDetails = () => navigate(`../viewgoal/${slug}`);
  return (
    <div
      style={{ width: 150, minHeight: 150, cursor: "pointer" }}
      onClick={viewGoalDetails}
    >
      <Stack
        direction="column"
        alignItems="center"
        justifyContent="flex-start"
        spacing={1}
        key={id}
      >
        <Image src={image} alt={title} />
        <TitleText>{title}</TitleText>
      </Stack>
    </div>
  );
};

export default SponseredGoalsItem;

const Image = styled("img")({
  width: 110,
  height: 110,
  border: "solid",
  borderColor: "#007AFF #48CAE4 #6EEDD6",
  borderRadius: 20,
  borderWidth: 2,
});

const TitleText = styled(Typography)({
  color: "#000",
  fontSize: 12,
  fontWeight: 200,
  textAlign: "center",
  width: 150,
});
