import { Grid } from "@material-ui/core";
import React from "react";
import { PostsType } from "./data";
import { makeStyles } from "@material-ui/styles";
import { useNavigate } from "react-router";

const VideoItem = ({ image, slug }: PostsType) => {
  const classes = useStyles();
  let navigate = useNavigate();
  return (
    <Grid
      container
      key={image}
      style={{
        backgroundImage: `url(${image})`,
        backgroundSize: "cover",
        backgroundPosition: "center",
        cursor: "pointer",
      }}
      className={classes.rootVideoItem}
      onClick={() => navigate(`../viewgoal/${slug}`)}
    >
      {/* <img src={PlayImage} alt="playButton" /> */}
    </Grid>
  );
};

export default VideoItem;
const useStyles = makeStyles(() => ({
  rootVideoItem: {
    height: 487,
    width: 262,
    margin: 5,
    marginRight: 0,
    borderRadius: 6,
    justifyContent: "center",
    alignItems: "center",
    alignSelf: "center",
    borderWidth: 3,
    borderColor:
      "rgb(146, 96, 204) rgb(71, 175, 228) rgb(46, 128, 177) rgb(87, 88, 198)",
    borderStyle: "solid",
  },
}));
