import { Stack, Typography, Button } from "@material-ui/core";
import React, { useState,useRef } from "react";
import { styled } from "@material-ui/styles";

import { StyledAvatar } from "./StyledAvatar";
import { FOLLOW_UNFOLLOW_API } from "../../../../../utils/Api";
import { useNavigate } from "react-router";
import { useSelector } from "react-redux";
import {
  AddFunction,
  UserDetailProps,
  RootAppState,
} from "../../../../../utils/typeHelpers";
import { useMutation } from "react-query";

interface ProfileProps {
  userInfo: UserDetailProps;
  handleRefresh: ()=>void;
}

const BasicInfo = ({ userInfo, handleRefresh}: ProfileProps) => {
  const refError = useRef<null | AddFunction>(null);
  const navigate = useNavigate();
  const userState = useSelector((state: RootAppState) => state.AuthReducer);

  
 

  const handleCreateError = (msg: string) => {
    refError.current?.(msg);
  };
  // console.log(userInfo.id);

  const handleFollowUnfollow = async () => {
    if (userState.isLoggedIn) {
      FOLLOW_UNFOLLOW_API(
        "user-follow",
        userInfo.id,
        userState.userInfo.access
      ).then((res) => {
        console.log('BasicInfo: ',res);
        if (res.is_followed === true) {
          console.log("true");
          handleRefresh();
        } else if (res.is_followed === false) {
          console.log("false");
          handleRefresh();
        } else {
          handleCreateError("Something went wrong");
        }
      });
    } else {
      navigate("../login");
    }
  };

  return (
    <Stack
      direction="column"
      justifyContent="flex-start"
      alignItems="center"
      sx={{ paddingTop: 5 }}
    >
      <HeaderTitle>{userInfo.full_name}-{userInfo.id}</HeaderTitle>
      <HeaderSubTitle>@{userInfo.username}</HeaderSubTitle>
      <StyledAvatar alt="ProfileAvatar" src={userInfo.image} />
      {/* <DonePoint>1200</DonePoint> */}
      {/* <DonePointTitle>Donee Point</DonePointTitle> */}
      <Stack direction="row" justifyContent="center" alignItems="center">
        <FollowersTitle>{userInfo.total_follow_count} Followers</FollowersTitle>
        <FollowersTitle>
          {userInfo.total_following_count} Following
        </FollowersTitle>
      </Stack>
      <GradientButton onClick={handleFollowUnfollow}>Follow</GradientButton>
    </Stack>
  );
};

export default BasicInfo;
const HeaderTitle = styled(Typography)({
  fontWeight: 700,
  fontSize: 32,
  wordWrap: "break-word",
  width: 350,
  textAlign: "center",
});
const HeaderSubTitle = styled(Typography)({
  color: "#007AFF",
  fontSize: 16,
});

const DonePoint = styled(Typography)({
  color: "#48CAE4",
  fontSize: 24,
  fontWeight: 600,
});

const DonePointTitle = styled(Typography)({
  color: "#48CAE4",
  fontSize: 16,
  fontWeight: 400,
  lineHeight: 0.4,
});

const FollowersTitle = styled(Typography)({
  color: "#000000",
  fontSize: 16,
  fontWeight: 500,
  padding: 10,
});
const GradientButton = styled(Button)({
  margin: 10,
  border: "2px solid",
  borderImageSlice: 1,
  borderImageSource: "linear-gradient(to left, #007AFF, #48CAE4, #6EEDD6)",
  minWidth: 220,
});
