import { Button, Stack, Typography } from "@material-ui/core";
import { styled } from "@material-ui/styles";

import React from "react";

const DonationContainer = () => {
  return (
    <DonationStyledContainer
      direction="column"
      justifyContent="flex-start"
      alignItems="center"
    >
      <TitleText>
        Some goals need <SpanText>a team </SpanText> to come true!
      </TitleText>
      <ButtonDonationTeam>Create Team Donation</ButtonDonationTeam>
    </DonationStyledContainer>
  );
};

export default DonationContainer;

const DonationStyledContainer = styled(Stack)({
  backgroundColor: "#EEF8FF",
  borderRadius: 10,
  boxShadow: " 0px 4px 8px rgba(0, 0, 0, 0.15)",
  maxWidth: 380,
  height: 150,
  marginBottom: 10,
});

const TitleText = styled(Typography)({
  color: "#6C757D",
  fontSize: 20,
  fontWeight: 600,
  textAlign: "center",
  padding: 20,
});

const SpanText = styled("span")({
  color: "#6EEDD6",
  fontSize: 20,
  fontWeight: 600,
  textAlign: "center",
});

const ButtonDonationTeam = styled(Button)({
  backgroundColor: "#6EEDD6",
  minWidth: 200,
  borderRadius: 20,
  color: "#fff",
});
