import { Grid, Container } from "@material-ui/core";
import React, { useState, useEffect } from "react";
import { ErrorBoundary } from "react-error-boundary";
import { useQuery, UseQueryResult } from "react-query";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router"; 

import { RootStyle } from "../../../../components/RootStyle";
// import { GET_DONOR_PROFILE_API } from "../../../../utils/Api";
import { RootAppState, UserDetailProps } from "../../../../utils/typeHelpers";
import PageErrorBoundary from "../../../Errror/PageErrorBoundary";
import LeftContainer from "./Components/LeftContainer";
import RightContainer from "./Components/RIghtContainer";

import ErrorCatchComponent from "../../../../components/ErrorCatchComponent";
import ApiErrorPage from "../../../Errror/ApiErrorPage";
import Loader from "../../../../components/Loader";
import useAxios from "../../../../utils/useAxios";

const DonorProfile = () => {
  const navigate = useNavigate();
  const userState = useSelector((state: RootAppState) => state.AuthReducer);
  const [followStatusForRefresh, setFollowStatusForRefresh] = useState(false);

  const handleRefresh = () => {
    setFollowStatusForRefresh(!followStatusForRefresh)
  }

  useEffect(() => {
    if (!userState.userInfo.is_account_created) {
      navigate("../createprofiledonor");
    }
  }, [userState.userInfo.is_account_created]);

  // const { data, isLoading }: UseQueryResult<UserDetailProps, Error> = useQuery(
  //   ["userInfo", userState.userInfo.access, followStatusForRefresh],
  //   () => GET_DONOR_PROFILE_API(userState.userInfo.access)
  // );

  const axiosInstance = useAxios();
  const { data, isLoading, isError, error }: UseQueryResult<UserDetailProps, Error> = useQuery(
    ["userInfo", userState.userInfo.access, followStatusForRefresh],
    async () => {
      const response = await axiosInstance.get(`/user-detail/`);

      return response.data;
    }
  );

  // console.log('donor--===--data: ', data);
  

  const [explode, setExplode] = useState(false);

  // if (isLoading) {
  //   return <Loader />;
  // }

  return (
    <Container>
    <ErrorCatchComponent>
      {isLoading === false ? (
        <div>
          {isError ? (
            <ApiErrorPage
              error={error ? error : new Error("Network response was not ok.")}
            />
          ) : (
            <ErrorBoundary
              fallbackRender={({ error, resetErrorBoundary }) => (
                <PageErrorBoundary
                  error={error}
                  resetErrorBoundary={resetErrorBoundary}
                />
              )}
              onReset={() => setExplode(false)}
              resetKeys={[explode]}
            >
              <RootStyle>
                <Grid container component="main" sx={{ minHeight: "100vh" }}>
                  {data && <LeftContainer userInfo={data} handleRefresh={handleRefresh}/>}
                  {data?.user_payment && data?.goalsave_user && (
                    <RightContainer
                      supportedGoals={data?.user_payment}
                      savedGoals={data?.goalsave_user}
                    />
                  )}
                </Grid>
              </RootStyle>
            </ErrorBoundary>
          )}
        </div>
      ) : (
        <Loader />
      )}
    </ErrorCatchComponent>
    </Container>
  );
};

export default DonorProfile;
